import { Row, Col, Button} from 'react-bootstrap';
import "./Products.scss";

function Products() {
    
  return (
    <div className="products-content">
        <Row className="pb-2">
          <Col sm={12} className="pb-2">
            <section className="bg-img-products rounded-2"></section>
          </Col>
        </Row>
        <Row >
          <Col sm={12} className="pb-2 ">
            <div className='bg-white rounded-2 p-3'>
              <Row>
                <Col sm={12} md={3} className="text-center">
                  <img className="w-50 rounded-2"
                    src={require("../../assets/images/products_personal_loan.png")}
                    alt="Personal loan"/>
                </Col>
                <Col sm={12} md={3}>
                <Col sm={12} className="text-center products-label">
                    <strong>Personal Loan</strong>
                  </Col>
                  <Col sm={12} className="text-center products-label">
                    <strong>@ 10.5%</strong>
                  </Col>
                  <Col sm={12} className="text-center">
                    <Button className="product-button" variant="primary" size="sm">Apply Now</Button>
                  </Col>
                </Col>
                <Col sm={12} md={3} className="text-center">
                  <img className="w-50 rounded-2"
                    src={require("../../assets/images/products_car_loan.png")}
                    alt="Car loan"/>
                </Col>
                <Col sm={12} md={3}>
                <Col sm={12} className="text-center products-label">
                    <strong>Car Loan</strong>
                  </Col>
                  <Col sm={12} className="text-center products-label">
                    <strong>@ 7.95%</strong>
                  </Col>
                  <Col sm={12} className="text-center">
                    <Button className="product-button" variant="primary" size="sm">Apply Now</Button>
                  </Col>
                </Col>
              </Row>
              
            </div>
          </Col>
        </Row>
    </div>
  );
}

export default Products;
